define('dummy/controllers/pie', ['exports', 'ember', 'dummy/controllers/slide', 'dummy/models/data', 'dummy/models/single_group/asset_values', 'dummy/models/single_group/many_values', 'dummy/models/time_series/monthly_return_single_period', 'dummy/models/single_group/high_net_worth_duration', 'dummy/models/single_group/one_value', 'dummy/models/single_group/two_values', 'dummy/models/single_group/zero', 'dummy/models/single_group/zeroes', 'dummy/models/single_group/sum_to_zero', 'dummy/models/single_group/bad_range'], function (exports, Ember, SlideController, data, asset_values, many_values, monthly_return_single_period, high_net_worth_duration, one_value, two_values, zero, zeroes, sum_to_zero, bad_range) {

  'use strict';

  exports['default'] = SlideController['default'].extend({

    // ---------
    // Default Settings
    // ---------

    maxNumberOfSlices: 8,
    minSlicePercent: 2,
    maxRadius: 100,

    // ---------
    // Data Selection
    // ---------

    availableDataSets: Ember['default'].computed('rawDataHash', function () {
      return Ember['default'].A(_.keys(this.get('rawDataHash')));
    }),

    data: Ember['default'].computed('selectedData', 'rawDataHash', function () {
      return this.get('rawDataHash')[this.get('selectedData')];
    }),

    rawDataHash: Ember['default'].computed(function () {
      return {
        asset_values: asset_values['default'],
        many_values: many_values['default'],
        monthly_return_single_period: monthly_return_single_period['default'],
        high_net_worth_duration: high_net_worth_duration['default'],
        '----': data['default']['null'],
        empty: data['default'].empty,
        one_value: one_value['default'],
        two_values: two_values['default'],
        zero: zero['default'],
        zeroes: zeroes['default'],
        sum_to_zero: sum_to_zero['default'],
        bad_range: bad_range['default']
      };
    }),

    selectedData: 'asset_values'

  });

});