define('dummy/tests/helpers/async/dom-trigger-event', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports['default'] = Ember['default'].Test.registerAsyncHelper('domTriggerEvent', function (app, selector, eventName) {
    var element = $(selector)[0];
    var event = document.createEvent('SVGEvents');
    event.initEvent(eventName, true, true);
    return element.dispatchEvent(event);
  });

});