define('dummy/tests/acceptance/pie-test', ['ember', 'qunit', 'dummy/tests/helpers/start-app', 'dummy/tests/helpers/utils/selector-for'], function (Ember, qunit, startApp, selector_for) {

  'use strict';

  var application;

  qunit.module('Acceptance | pie', {
    beforeEach: function beforeEach() {
      application = startApp['default']();
      visit('/pie');
    },

    afterEach: function afterEach() {
      Ember['default'].run(application, 'destroy');
    }
  });

  var countArcs = function countArcs() {
    return $('.chart-pie svg g.arc').length;
  };

  qunit.test('tooltip mouseover with data: asset_values', function (assert) {
    assert.expect(4);

    fillIn(selector_for.emberSelectFor('data'), 'asset_values');
    domTriggerEvent('g.arc', 'mouseover');
    andThen(function () {
      assertTooltip(assert, {
        isVisible: true,
        label: 'Private Equity',
        name: 'Value: ',
        value: '1,574,677.59'
      });
    });
  });

  qunit.test('tooltip mouseout with data: asset_values', function (assert) {
    assert.expect(4);
    fillIn(selector_for.emberSelectFor('data'), 'asset_values');
    domTriggerEvent('.arc', 'mouseover');
    domTriggerEvent('.arc', 'mouseout');

    andThen(function () {
      assertTooltip(assert, { isVisible: false });
    });
  });

  qunit.test('select data: asset_values', function (assert) {
    assert.expect(1);
    fillIn(selector_for.emberSelectFor('data'), 'asset_values');
    andThen(function () {
      assert.equal(countArcs(), 6);
    });
  });

  qunit.test('select data: many_values', function (assert) {
    assert.expect(1);
    fillIn(selector_for.emberSelectFor('data'), 'many_values');
    andThen(function () {
      assert.equal(countArcs(), 8);
    });
  });

  qunit.test('select data: monthly_return_single_period', function (assert) {
    assert.expect(1);
    fillIn(selector_for.emberSelectFor('data'), 'monthly_return_single_period');
    andThen(function () {
      assert.equal(countArcs(), 7);
    });
  });

  qunit.test('select data: high_net_worth_duration', function (assert) {
    assert.expect(1);
    fillIn(selector_for.emberSelectFor('data'), 'high_net_worth_duration');
    andThen(function () {
      assert.equal(countArcs(), 8);
    });
  });

  qunit.test('select data: null data', function (assert) {
    assert.expect(1);
    fillIn(selector_for.emberSelectFor('data'), '----');
    andThen(function () {
      assert.equal(countArcs(), 0);
    });
  });

  qunit.test('select data: empty data', function (assert) {
    assert.expect(1);
    fillIn(selector_for.emberSelectFor('data'), 'empty');
    andThen(function () {
      assert.equal(countArcs(), 0);
    });
  });

  qunit.test('select data: two_values', function (assert) {
    assert.expect(1);
    fillIn(selector_for.emberSelectFor('data'), 'two_values');
    andThen(function () {
      assert.equal(countArcs(), 1);
    });
  });

  qunit.test('select data: bad_range', function (assert) {
    assert.expect(1);
    fillIn(selector_for.emberSelectFor('data'), 'bad_range');
    andThen(function () {
      assert.equal(countArcs(), 3);
    });
  });

});