define('dummy/controllers/vertical-bar', ['exports', 'ember', 'dummy/controllers/slide', 'dummy/models/data', 'dummy/models/multi_group/two_ranges', 'dummy/models/multi_group/three_ranges', 'dummy/models/multi_group/five_ranges', 'dummy/models/multi_group/sector_compare_return', 'dummy/models/single_group/asset_values', 'dummy/models/single_group/many_values', 'dummy/models/time_series/monthly_return_single_period', 'dummy/models/single_group/high_net_worth_duration', 'dummy/models/single_group/one_value', 'dummy/models/single_group/two_values', 'dummy/models/single_group/zero', 'dummy/models/single_group/zeroes', 'dummy/models/single_group/sum_to_zero', 'dummy/models/single_group/bad_range'], function (exports, Ember, SlideController, data, two_ranges, three_ranges, five_ranges, sector_compare_return, asset_values, many_values, monthly_return_single_period, high_net_worth_duration, one_value, two_values, zero, zeroes, sum_to_zero, bad_range) {

  'use strict';

  exports['default'] = SlideController['default'].extend({

    // ---------
    // Default Settings
    // ---------

    betweenGroupPadding: 0,
    withinGroupPadding: 0,
    maxLabelHeight: 40,
    stackBars: false,

    // ---------
    // Data Selection
    // ---------

    availableDataSets: Ember['default'].computed('rawDataHash', function () {
      return Ember['default'].A(_.keys(this.get('rawDataHash')));
    }),

    data: Ember['default'].computed('selectedData', 'rawDataHash', function () {
      return this.get('rawDataHash')[this.get('selectedData')];
    }),

    rawDataHash: Ember['default'].computed(function () {
      return {
        two_ranges: two_ranges['default'],
        three_ranges: three_ranges['default'],
        five_ranges: five_ranges['default'],
        sector_compare_return: sector_compare_return['default'],
        '----': data['default']['null'],
        asset_values: asset_values['default'],
        many_values: many_values['default'],
        monthly_return_single_period: monthly_return_single_period['default'],
        high_net_worth_duration: high_net_worth_duration['default'],

        empty: data['default'].empty,
        one_value: one_value['default'],
        two_values: two_values['default'],
        zero: zero['default'],
        zeroes: zeroes['default'],
        sum_to_zero: sum_to_zero['default'],
        bad_range: bad_range['default']
      };
    }),

    selectedData: 'five_ranges'

  });

});