define('dummy/controllers/scatter', ['exports', 'ember', 'dummy/controllers/slide', 'dummy/models/data', 'dummy/models/multi_value/grouped_percent', 'dummy/models/multi_value/grouped_money', 'dummy/models/multi_value/ungrouped_percent', 'dummy/models/multi_value/ungrouped_money', 'dummy/models/multi_value/grouped_zero', 'dummy/models/multi_value/grouped_zeroes', 'dummy/models/multi_value/ungrouped_zero', 'dummy/models/multi_value/ungrouped_zeroes'], function (exports, Ember, SlideController, models__data, grouped_percent, grouped_money, ungrouped_percent, ungrouped_money, grouped_zero, grouped_zeroes, ungrouped_zero, ungrouped_zeroes) {

  'use strict';

  exports['default'] = SlideController['default'].extend({

    // ---------
    // Data Selection
    // ---------

    availableDataSets: Ember['default'].computed('rawDataHash', function () {
      return Ember['default'].A(_.keys(this.get('rawDataHash')));
    }),

    data: Ember['default'].computed('selectedData', 'rawDataHash', function () {
      return this.get('rawDataHash')[this.get('selectedData')];
    }),

    isShowingTotal: false,

    xValueDisplayName: 'Risk',
    yValueDisplayName: 'Return',

    // A simple default portfolio total calculation by adding both factors. Often
    // the portfolio total is a separate data source provided as a reference.
    totalPointData: Ember['default'].computed('data', function () {
      var data = this.get('data');
      return {
        group: 'Portfolio Total',
        xValue: _.reduce(data, function (prev, d) {
          return prev + d.xValue;
        }, 0),
        yValue: _.reduce(data, function (prev, d) {
          return prev + d.yValue;
        }, 0)
      };
    }),

    // Select which raw data we will pull from
    selectedData: 'groupedPercent',
    dotRadius: 7,

    rawDataHash: Ember['default'].computed(function () {
      return {
        groupedPercent: grouped_percent['default'],
        groupedMoney: grouped_money['default'],
        ungroupedPercent: ungrouped_percent['default'],
        ungroupedMoney: ungrouped_money['default'],
        '----': models__data['default']['null'],
        empty: models__data['default'].empty,
        groupedZero: grouped_zero['default'],
        groupedZeroes: grouped_zeroes['default'],
        ungroupedZero: ungrouped_zero['default'],
        ungroupedZeroes: ungrouped_zeroes['default']
      };
    })

  });

});