define('dummy/tests/acceptance/site-navigation-test', ['ember', 'qunit', 'dummy/tests/helpers/start-app'], function (Ember, qunit, startApp) {

  'use strict';

  var application;

  qunit.module('Acceptance | site navigation', {
    beforeEach: function beforeEach() {
      application = startApp['default']();
    },

    afterEach: function afterEach() {
      Ember['default'].run(application, 'destroy');
    }
  });

  qunit.test('visiting /', function (assert) {
    visit('/');
    andThen(function () {
      assert.equal(currentURL(), '/overview');
    });
  });

  qunit.test('visiting /overview', function (assert) {
    visit('/');
    andThen(function () {
      assert.equal(currentURL(), '/overview');
    });
  });

  qunit.test('visiting /time-series', function (assert) {
    visit('/time-series');
    andThen(function () {
      assert.equal(currentURL(), '/time-series');
    });
  });

  qunit.test('visiting /horizontal-bar', function (assert) {
    visit('/horizontal-bar');
    andThen(function () {
      assert.equal(currentURL(), '/horizontal-bar');
    });
  });

  qunit.test('visiting /pie', function (assert) {
    visit('/pie');
    andThen(function () {
      assert.equal(currentURL(), '/pie');
    });
  });

  qunit.test('visiting /scatter', function (assert) {
    visit('/scatter');
    andThen(function () {
      assert.equal(currentURL(), '/scatter');
    });
  });

});