define('dummy/tests/acceptance/horizontal-bar-test', ['ember', 'qunit', 'dummy/tests/helpers/start-app', 'dummy/tests/helpers/utils/selector-for'], function (Ember, qunit, startApp, selector_for) {

  'use strict';

  var application;

  qunit.module('Acceptance | horizontal-bar', {
    beforeEach: function beforeEach() {
      application = startApp['default']();
      visit('/horizontal-bar');
    },

    afterEach: function afterEach() {
      Ember['default'].run(application, 'destroy');
    }
  });

  var countBars = function countBars() {
    return $('.chart-horizontal-bar svg g.bar').length;
  };

  var countRects = function countRects() {
    return $('.chart-horizontal-bar svg rect').length;
  };

  qunit.test('tooltip mouseover with bars: asset_values', function (assert) {
    assert.expect(4);

    fillIn(selector_for.emberSelectFor('data'), 'asset_values');
    domTriggerEvent('.bar rect', 'mouseover');
    andThen(function () {
      assertTooltip(assert, {
        isVisible: true,
        label: 'Private Equity',
        name: 'Value: ',
        value: '1,574,677.59'
      });
    });
  });

  qunit.test('tooltip mouseout with bars: asset_values', function (assert) {
    assert.expect(4);
    fillIn(selector_for.emberSelectFor('data'), 'asset_values');
    domTriggerEvent('.bar rect', 'mouseover');
    domTriggerEvent('.bar rect', 'mouseout');

    andThen(function () {
      assertTooltip(assert, { isVisible: false });
    });
  });

  qunit.test('select bars: asset_values', function (assert) {
    assert.expect(2);
    fillIn(selector_for.emberSelectFor('data'), 'asset_values');
    andThen(function () {
      assert.equal(countBars(), 6);
      assert.equal(countRects(), 6);
    });
  });

  qunit.test('select bars: many_values', function (assert) {
    assert.expect(2);
    fillIn(selector_for.emberSelectFor('data'), 'many_values');
    andThen(function () {
      assert.equal(countBars(), 26);
      assert.equal(countRects(), 26);
    });
  });

  qunit.test('select bars: monthly_return_single_period', function (assert) {
    assert.expect(2);
    fillIn(selector_for.emberSelectFor('data'), 'monthly_return_single_period');
    andThen(function () {
      assert.equal(countBars(), 7);
      assert.equal(countRects(), 7);
    });
  });

  qunit.test('select bars: high_net_worth_duration', function (assert) {
    assert.expect(2);
    fillIn(selector_for.emberSelectFor('data'), 'high_net_worth_duration');
    andThen(function () {
      assert.equal(countBars(), 8);
      assert.equal(countRects(), 8);
    });
  });

  qunit.test('select bars: null data', function (assert) {
    assert.expect(2);
    fillIn(selector_for.emberSelectFor('data'), '----');
    andThen(function () {
      assert.equal(countBars(), 0);
      assert.equal(countRects(), 0);
    });
  });

  qunit.test('select bars: empty data', function (assert) {
    assert.expect(2);
    fillIn(selector_for.emberSelectFor('data'), 'empty');
    andThen(function () {
      assert.equal(countBars(), 0);
      assert.equal(countRects(), 0);
    });
  });

  qunit.test('select bars: two_values', function (assert) {
    assert.expect(2);
    fillIn(selector_for.emberSelectFor('data'), 'two_values');
    andThen(function () {
      assert.equal(countBars(), 2);
      assert.equal(countRects(), 2);
    });
  });

});