define('dummy/tests/acceptance/vertical-bar-test', ['ember', 'qunit', 'dummy/tests/helpers/start-app', 'dummy/tests/helpers/utils/selector-for'], function (Ember, qunit, startApp, selector_for) {

  'use strict';

  var application;

  qunit.module('Acceptance | vertical-bar', {
    beforeEach: function beforeEach() {
      application = startApp['default']();
      visit('/vertical-bar');
    },

    afterEach: function afterEach() {
      Ember['default'].run(application, 'destroy');
    }
  });

  var countBars = function countBars() {
    return $('.chart-vertical-bar svg g.bars').length;
  };

  var countRects = function countRects() {
    return $('.chart-vertical-bar svg rect').length;
  };

  qunit.test('tooltip mouseover with bars: two_ranges', function (assert) {
    assert.expect(4);

    fillIn(selector_for.emberSelectFor('data'), 'two_ranges');
    domTriggerEvent('.grouping-0', 'mouseover');
    andThen(function () {

      assertTooltip(assert, {
        isVisible: true,
        label: 'S&P Goldman Sachs Commodity Total Return Index',
        name: 'Cash & Cash Equivalent: ',
        value: '1,933,418.12'
      });
    });
  });

  qunit.test('tooltip mouseout with bars: two_ranges', function (assert) {
    assert.expect(4);
    fillIn(selector_for.emberSelectFor('data'), 'two_ranges');
    domTriggerEvent('.grouping-0', 'mouseover');
    domTriggerEvent('.grouping-0', 'mouseout');

    andThen(function () {
      assertTooltip(assert, { isVisible: false });
    });
  });

  qunit.test('select bars: two_ranges', function (assert) {
    assert.expect(2);
    fillIn(selector_for.emberSelectFor('data'), 'two_ranges');
    andThen(function () {
      assert.equal(countBars(), 2);
      assert.equal(countRects(), 14);
    });
  });

  qunit.test('select bars: three_ranges', function (assert) {
    assert.expect(2);
    fillIn(selector_for.emberSelectFor('data'), 'three_ranges');
    andThen(function () {
      assert.equal(countBars(), 3);
      assert.equal(countRects(), 9);
    });
  });

  qunit.test('select bars: five_ranges', function (assert) {
    assert.expect(2);
    fillIn(selector_for.emberSelectFor('data'), 'five_ranges');
    andThen(function () {
      assert.equal(countBars(), 5);
      assert.equal(countRects(), 25);
    });
  });

  qunit.test('select bars: sector_compare_return', function (assert) {
    assert.expect(2);
    fillIn(selector_for.emberSelectFor('data'), 'sector_compare_return');
    andThen(function () {
      assert.equal(countBars(), 5);
      assert.equal(countRects(), 50);
    });
  });

  qunit.test('select bars: null data', function (assert) {
    assert.expect(2);
    fillIn(selector_for.emberSelectFor('data'), '----');
    andThen(function () {
      assert.equal(countBars(), 0);
      assert.equal(countRects(), 0);
    });
  });

  qunit.test('select bars: empty data', function (assert) {
    assert.expect(2);
    fillIn(selector_for.emberSelectFor('data'), 'empty');
    andThen(function () {
      assert.equal(countBars(), 0);
      assert.equal(countRects(), 0);
    });
  });

  qunit.test('select bars: asset_values', function (assert) {
    assert.expect(2);
    fillIn(selector_for.emberSelectFor('data'), 'asset_values');
    andThen(function () {
      assert.equal(countBars(), 6);
      assert.equal(countRects(), 6);
    });
  });

});